@import url(https://fonts.googleapis.com/css?family=Nunito:400,700&display=swap);
@import url(https://fonts.googleapis.com/css?family=Share+Tech+Mono&display=swap);
@import url(https://fonts.googleapis.com/css?family=Rubik&display=swap);
@import url(https://fonts.googleapis.com/css?family=Quicksand&display=swap);
@import url(https://fonts.googleapis.com/css?family=Alatsi&display=swap);
@import url(https://fonts.googleapis.com/css?family=Bebas+Neue&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App{background-color:#121616;height:100vh}
.Header-box{position:absolute;top:0;height:auto;margin:0 auto;padding:10px 3vw;display:block}.Header-box .img{width:auto;height:70px;background-size:contain;background-repeat:no-repeat;background-image:url(data:image/png;base64,)}
.Intro-box{height:700px;top:0;background:#0d1112;background:linear-gradient(to right, #0d1112 0%, #1d3130 45%, #136952 85%, #019664 100%);filter:progid:DXImageTransform.Microsoft.gradient( startColorstr="#0d1112", endColorstr="#198c71",GradientType=1 )}
